<template>
  <div class="minelist-body">
    <div class="minelist-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>我的</a-breadcrumb-item>
        <a-breadcrumb-item><a href="/mine">基本信息</a></a-breadcrumb-item>
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
      <hr />
    </div>
    <div class="minelist-section">
      <span>{{ this.$route.meta.title }}</span>
      <div>
        <span>工厂名称︰</span>
        <span>{{ mineitem.merName }}</span>
      </div>
      <div>
        <span>负责人：</span>
        <span>数据</span>
      </div>

      <div>
        <span>商标类型：</span>
        <span>{{ mineitem.trademarkType }}</span>
      </div>
      <div>
        <span>入驻类型：</span>
        <span>{{ mineitem.businessOperation }}</span>
      </div>
      <div>
        <span>营业执照：</span>

        <img
          width="100"
          height="100"
          v-for="(item, index) in mineurl"
          :key="index"
          :src="item.picUrl"
          alt=""
        />
      </div>
      <div>
        <span>商品质量证明：</span>
        <img
          width="100"
          height="100"
          v-for="(item, index) in minepic"
          :key="index"
          :src="item.picUrl"
          alt=""
        />
      </div>
    </div>
    <div class="minelist-footer"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mineitem: [],
      mineurl: [],
      minepic: [],
    };
  },
  mounted() {
    this.mineList();
    this.minePic(0);
    this.minePic(1);
  },
  methods: {
    async mineList() {
      let str = {
        id: sessionStorage.getItem("id"),
      };
      let { data } = await this.$http.getmer(str);
      if (data.code == "0000") {
        this.mineitem = data.data.mer.merInfo;
        this.status();
      }
    },
    async minePic(type) {
      let str = {
        merId: sessionStorage.getItem("id"),
        picType: type,
      };
      let { data } = await this.$http.merpic(str);
      console.log(data);
      if (data.code == "0000") {
        if (type == 0) {
          let minstr = new Array();
          minstr = data.data.list;
          minstr.reverse();
          console.log(minstr);
          this.mineurl = minstr.slice(0, 2);
          console.log(this.mineurl);
        }
        if (type == 1) {
          let minlist = new Array();
          minlist = data.data.list;
          minlist.reverse();
          this.minepic = minlist.slice(0, 2);
          console.log(this.minepic);
        }
      }
    },
    status() {
      switch (this.mineitem.trademarkType) {
        case 0:
          this.mineitem.trademarkType = "自由品牌";
          break;
        case 1:
          this.mineitem.trademarkType = "共有品牌";
          break;
        case 2:
          this.mineitem.trademarkType = "加盟蓝天";
          break;
      }
    },
  },
};
</script>

<style scoped>
.minelist-section div {
  border-top: 0;
  border: 1px solid #c0c0c0;
  padding: 10px 10px 10px 180px;
}
.minelist-section > :nth-child(1) {
  width: 100%;
  height: 40px;
  line-height: 40px;
  background: #c0c0c0;
  font-size: 15px;
  font-weight: 900;
  display: block;
}
.minelist-section div > :nth-child(1) {
  display: inline-block;
  width: 10%;
  text-align: right;
}
.minelist-section img {
  margin-left: 10px;
}
</style>